html {
  scroll-behavior: smooth;
}

*, *::before, *::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

input, select, button, textarea {
  font: inherit;
}

img, svg, picture, video {
  display: block;
  max-width: 100%;
}

ul, ol {
  list-style: none;
}

body {
  min-height: 100vh;
}

body {
  font-family: "Hanken Grotesk", sans-serif;
  font-size: 1rem;
  line-height: 1.303125rem;
}
@media (min-width: 40rem) {
  body {
    font-size: 1.125rem;
    line-height: 1.465625rem;
  }
}

.font-xl {
  font-size: 3.5rem;
  line-height: 4.5rem;
  font-weight: 800;
}
@media (min-width: 40rem) {
  .font-xl {
    font-size: 4.5rem;
  }
}

.font-l {
  font-size: 1.5rem;
  line-height: 1.954375rem;
}
@media (min-width: 40rem) {
  .font-l {
    font-size: 2rem;
    line-height: 2.60625rem;
  }
}

.font-m {
  font-size: 1.125rem;
  line-height: 1.465625rem;
}
@media (min-width: 40rem) {
  .font-m {
    font-size: 1.5rem;
    line-height: 1.954375rem;
  }
}

a {
  text-decoration: none;
}

.container {
  max-width: 46rem;
  margin: 0 auto;
}

.component {
  background-color: hsl(0, 0%, 100%);
}
@media (min-width: 40rem) {
  .component {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 2rem;
  }
}
.component__result {
  flex: 1;
  background: linear-gradient(180deg, hsl(252, 100%, 67%) 0%, hsl(252, 100%, 67%) 0.01%, hsl(241, 81%, 54%) 100%);
  border-radius: 0 0 2rem 2rem;
  padding: 1.5rem 3.625rem 2.5rem;
  color: hsl(241, 100%, 89%);
  text-align: center;
}
@media (min-width: 40rem) {
  .component__result {
    padding: 2.375rem 3.375rem 3.4375rem;
    border-radius: 2rem;
  }
}
.component__result-title {
  margin-bottom: 1.5rem;
}
@media (min-width: 40rem) {
  .component__result-title {
    margin-bottom: 2.1875rem;
  }
}
.component__result-value {
  background: linear-gradient(180deg, hsl(256, 72%, 46%) 0%, hsla(241, 72%, 46%, 0) 100%, hsla(241, 72%, 46%, 0) 100%);
  padding: 1.5625rem 2.375rem 1.375rem;
  width: fit-content;
  margin: 0 auto;
  border-radius: 50%;
  font-weight: 700;
  color: hsla(241, 100%, 89%, 0.5);
  margin-bottom: 1.5rem;
}
@media (min-width: 40rem) {
  .component__result-value {
    padding: 3.4375rem 3.75rem 2.875rem;
    margin-bottom: 1.75rem;
  }
}
.component__result-value span {
  display: block;
  color: hsl(0, 0%, 100%);
  margin-bottom: 0.25rem;
}
.component__result-message {
  color: hsl(0, 0%, 100%);
  font-weight: 700;
  margin-bottom: 0.5rem;
}
@media (min-width: 40rem) {
  .component__result-message {
    margin-bottom: 0.875rem;
  }
}
.component__summary {
  flex: 1;
  padding: 1.5rem 1.875rem 1.875rem;
}
@media (min-width: 40rem) {
  .component__summary {
    padding: 2.375rem 2.5rem 2.875rem;
  }
}
.component__summary-title {
  color: hsl(224, 30%, 27%);
  margin-bottom: 1.5rem;
}
@media (min-width: 40rem) {
  .component__summary-title {
    margin-bottom: 1.75rem;
  }
}
.component__summary-items {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 1.5rem;
}
@media (min-width: 40rem) {
  .component__summary-items {
    margin-bottom: 2.5rem;
  }
}
.component__summary-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  border-radius: 0.75rem;
}
.component__summary-item.item-reaction {
  background-color: hsla(0, 100%, 67%, 0.1);
}
.component__summary-item.item-reaction .component__summary-item-type {
  color: hsl(0, 100%, 67%);
}
.component__summary-item.item-memory {
  background-color: hsla(39, 100%, 56%, 0.1);
}
.component__summary-item.item-memory .component__summary-item-type {
  color: hsl(39, 100%, 56%);
}
.component__summary-item.item-verbal {
  background-color: hsla(166, 100%, 37%, 0.1);
}
.component__summary-item.item-verbal .component__summary-item-type {
  color: hsl(166, 100%, 37%);
}
.component__summary-item.item-visual {
  background-color: hsla(234, 85%, 45%, 0.1);
}
.component__summary-item.item-visual .component__summary-item-type {
  color: hsl(234, 85%, 45%);
}
.component__summary-item-type {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
}
.component__summary-item-value {
  font-weight: 700;
  color: hsla(224, 30%, 27%, 0.5);
}
.component__summary-item-value span {
  display: inline-block;
  color: hsl(224, 30%, 27%);
  margin-right: 0.5rem;
}
.component__summary-button {
  position: relative;
  display: block;
  background: hsl(224, 30%, 27%);
  color: hsl(0, 0%, 100%);
  text-align: center;
  font-weight: 700;
  padding: 1.0625rem;
  border-radius: 8rem;
  overflow: hidden;
  z-index: 99;
  transition: opacity 200ms ease-in-out;
}
.component__summary-button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, hsl(252, 100%, 67%) 0%, hsl(252, 100%, 67%) 0.01%, hsl(241, 81%, 54%) 100%);
  opacity: 0;
  z-index: -1;
  transition: all 200ms ease-in-out;
}
.component__summary-button:hover::before {
  opacity: 1;
}

body {
  background-color: hsl(221, 100%, 96%);
}
@media (min-width: 40rem) {
  body {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}